<template>
  <div class="detail">
    <Mheader :back="true">详情</Mheader>
    <ul class="contains">
      <li>
        <label for="bookname">图书名称</label>
        <input type="text" v-model="book.bookName" name="bookname">
      </li>
      <li>
        <label for="bookinfo">图书介绍</label>
        <input type="text" v-model="book.bookInfo" name="bookinfo">
      </li>
      <li>
        <label for="bookprice">图书价格</label>
        <input type="text" v-model.number="book.bookPrice" name="bookprice">
      </li>
      <button @click="change">确认修改</button>
    </ul>
  </div>
</template>
<script>
  import Mheader  from '../base/Mheader.vue'
  import {getoneBook,updateBook} from '../api'
  export default {
    data(){
      return {
        book:[]
      }
    },
    watch:{
      $route(){
        this.getData()
      }
    },
    created(){
     this.getData()
    },
    methods:{
       async change(){
        await updateBook(this.bid,this.book);
        this.$router.push('/list');
      },
      async getData(){
        this.book=await getoneBook(this.bid);
        Object.keys(this.book).length>0?void 0:this.$router.push('/list');
      }
    },
    computed:{
      bid(){
        return this.$route.params.bid;
      }
    },
    components:{
      Mheader,updateBook
    }
  }
</script>
<style scoped lang="less">
  .detail{
    position: absolute;
    top:0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 2000;
    background-color: #ffffff;
    .contains{
      margin-top: 40px;
      padding:30px;
      li{

        margin-bottom: 20px;
      };
      label{
        display: block;
        font-size: 20px;
        margin-bottom: 10px;
      }
      input{
        display: block;
        width: 100%;
        font-size: 16px;
        line-height: 16px;
        padding: 5px;
      }
      button{
        font-size: 16px;
        color: #ffffff;
        line-height: 16px;
        padding: 10px;
        border: none;
        border-radius: 5px;
        background-color: orangered;
      }
    }
  }
</style>



